import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Avatar from '@mui/material/Avatar';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import {makeStyles} from '@mui/styles';
import '../../../App.css';
import {
  Dialog,
  DialogContent,
  DialogActions,
  DialogTitle,
  collapseClasses,
} from '@mui/material';
import { useState } from 'react';
import SignUp from '../signup/signup.jsx';
import {data as languageInfo} from "./data.jsx"
const imageList = ["data:image/jpeg;base64,","https://www.techexplorist.com/wp-content/uploads/2020/07/earth-continents-1.jpg","https://images.pexels.com/photos/1766838/pexels-photo-1766838.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1","https://mir-s3-cdn-cf.behance.net/project_modules/1400/4b8001191956669.65d464c2e1061.jpg"]

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundImage:  `linear-gradient(rgba(0, 0, 0, 0.527), rgba(0, 0, 0, 0.5)), url(${imageList[2]})`,
    // `url(${imageList[1]})`
    backgroundColor: `gray`,
    backgroundSize: 'cover',
    minHeight: '100vh',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    padding: theme.spacing(3),
    boxSizing: 'border-box',
    color: 'white',
    textAlign: 'center',
  },
  contentBox: {
    backgroundColor: 'rgba(255, 255, 255, 0.95)',
    paddingTop: theme.spacing(5),
    paddingBottom: theme.spacing(17),
    borderRadius: theme.spacing(1),
    color : "black",
  },
  textField : {
    [theme.breakpoints.up('sm')]: {
      width : "80%"
    },[theme.breakpoints.down("sm")]: {
      width : "100%"
    },
  }
}));


export default function SignIn(props) {
  const languageText = languageInfo[props.language]
  const [signInData, setSignInData] = useState({
    userid: "",
    password:""
  })
  function changeValue(e){
    let copy = {...signInData }
    copy[e.target.name]  = e.target.value
    setSignInData(copy)
  }

  const onsubmit= async(e)=>{
    e.preventDefault();
    console.log(signInData)
    try{
      const response = await fetch("http://leadershipsurvey.pythonanywhere.com/login/",
      {
        method : "POST",
        headers :{
          "Content-Type" : "application/json"
        },
        body : JSON.stringify(signInData)
      })

    const data = await response.json();
    console.log(data)
    if (data.message == "success"){
      console.log("로그인성공");
      const token = data.token;
      localStorage.setItem('token', token);
      window.location.href = "/home"
    }else if (data.message == "ID or password is incorrect"){
      window.alert("ID or password is incorrect");
      setSignInData({
        userid: "",
        password:""
      })
    }} catch (error) {

      console.error('오류:', error);
    }
  }
  const classes = useStyles()
  const [open, openChange] = useState(false);
  const [findIdOpen, findIdOpenChange] = useState(false);
  const [findPwOpen, findPwOpenChange] = useState(false);
  const openSignUpDialog = () => {
    openChange(true);
  };
  const closeSignUpDialog = () => {
    openChange(false);
  };
  const openFindID = () => {
    findIdOpenChange(true);
  };
  const closeFindID = () => {
    findIdOpenChange(false);
  };
  const openFindPW = () => {
    findPwOpenChange(true);
  };
  const closeFindPW = () => {
    findPwOpenChange(false);
  };
  return (
    <div className={classes.root}>

    <Container maxWidth="sm" className = {classes.contentBox}>
      <Box
        sx={{
          mt: 8,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}
      >
        <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5" >
          {languageText.title}
        </Typography>
        <form action = "/userCheckToSignIn" onSubmit={onsubmit}>
        <TextField
          value = {signInData.userid}
          margin="normal"
          label=          {languageText.id}

          name="userid"
          required
          autoFocus
          className = {classes.textField}
          onChange = {changeValue}
          sx = {{mt : 6}}
        />
        <TextField
          value = {signInData.password}
          label= {languageText.password}
          type="password"
          name="password"
          required
          className = {classes.textField}
          onChange = {changeValue}

          autoComplete="current-password"
        />
        <FormControlLabel
          control={<Checkbox value="remember" />}
          label={languageText.rememberid}
        />
        <Button
          type="submit"
          variant="contained"
          className = {classes.textField}
          sx = {{mt : 1}}
        >
          {languageText.signinbtn}
        </Button>
        </form>
        <Grid container width = {{sm : "80%", xs : "100%"}}>
          <Grid item xs ={7} align = "left">
            <Link  fontSize ={{xs : 13, sm : 16}} onClick = {openFindID}>{languageText.forgotid}</Link>
            <Link sx = {{ml : 3}} fontSize ={{xs : 13, sm : 16}} onClick = {openFindPW}>{languageText.forgotpassword}</Link>
          </Grid>
          <Grid item xs ={5} align = "right">
            <Link   fontSize ={{xs : 13, sm : 16}}onClick={openSignUpDialog}>{languageText.signupbtn}</Link>
          </Grid>
        </Grid>
      </Box>

      <Dialog open={open} onClose={closeSignUpDialog} fullWidth maxWidth="xs">
        <DialogContent>
          <SignUp language = {props.language}/>
        </DialogContent>

      </Dialog>
      <Dialog open={findIdOpen} onClose={closeFindID} fullWidth maxWidth="xs">
        <DialogContent>
        </DialogContent>

      </Dialog>
      <Dialog open={findPwOpen} onClose={closeFindPW} fullWidth maxWidth="xs">
        <DialogContent>
        </DialogContent>

      </Dialog>
    </Container>
    </div>

  );
}
